import React from "react";
import * as M from '@mui/material'
import { styled } from '@mui/material/styles';
import { BrowserRouter as Router, Link, Route, Routes } from "react-router-dom";
import { ComplexityCalculator } from "../Calculator/Editor";
import { About } from "./about";
import { Purpose } from "./purpose";
import { Started } from "./get-started";
import { Contact } from "./contact";

export const Home = () => {
    return (
        <Router>
        <div className="container">
            <M.Container maxwidth="md">
            <div className="heading-links">
                <br />
                <M.Typography variant="h2" component="h2">
                    Time Complexity Calculator
                </M.Typography>
                <br />

                <M.Divider />
                <Link to="/time-complexity-study/about"><M.Button variant="text">About</M.Button></Link>
                <Link to="/time-complexity-study/purpose"><M.Button variant="text">Purpose</M.Button></Link>
                <Link to="/time-complexity-study/get-started"><M.Button variant="text">Get Started</M.Button></Link>
                <Link to="/time-complexity-study/contact"><M.Button variant="text">Contact</M.Button></Link>

                <M.Divider />
                <Link to="time-complexity-study/time-complexity-calculator">
                <M.Button variant="text">Calculate Time Complexity</M.Button>
                </Link>
                <M.Divider />
                <br />
                </div>

            </M.Container>
            <Routes>
            <Route exact path="/time-complexity-study/time-complexity-calculator" element={<ComplexityCalculator />} />
            <Route exact path="/time-complexity-study/about" element={<About />}></Route>
            <Route exact path="/time-complexity-study/purpose" element={<Purpose />}></Route>
            <Route exact path="/time-complexity-study/get-started" element={<Started />}></Route>
            <Route exact path="time-complexity-study/contact" element={<Contact />}></Route>
            </Routes>
        </div>
        </Router>
    )
}