import React from "react";
import * as M from '@mui/material'
import { styled } from '@mui/material/styles';


export const Contact = () => {
return(
    <div>
        <M.Container maxwidth="md">
        <M.Grid container spacing={2}>
            <M.Grid item xs={9}>
                <div>

                    <M.Typography variant="h3" component="h3">
                        Contact
                    </M.Typography>
                    <M.Divider /><br />
                    <M.Typography variant="body1" component="body">
                    Got a query? Get in contact with us -
                    <br /><br />
                    Email - innomightmail@gmail.com <br />
                    Mobile - +91 9755057765
                    </M.Typography>
                    </div></M.Grid>
                    <M.Grid item xs={3}>
                        Programming Quotes
                        <br />
                    <M.Divider />
                    <br />
                </M.Grid>
                </M.Grid>
                </M.Container>
    </div>
    )
}