import React from "react";
import * as M from '@mui/material'
import { styled } from '@mui/material/styles';


export const Started = () => {
return(
    <div>
        <M.Container maxwidth="md">
        <M.Grid container spacing={2}>
            <M.Grid item xs={9}>
                <div>
                    <M.Typography variant="h3" component="h3">
                        Get Started Documentation
                    </M.Typography>
                    <M.Divider /><br />
                    <M.Typography variant="body1" component="body">
                    This guide will help you in using this in the best way possible.
                    <br /><br />
                    You shall enter a function with a single parameter for now.<br />

                    The name of the function should be my_func.
                    <br />
                    Example: <br />
                    <br />
                    def my_func(n): <br /><br />
                    # Write your function code here. Leave 4 blanks as whitespace.
                    <br /> <br />
                    The output section will return the time complexity of your program in on of the below major time complexities - <br /><br />
                    O(1) - Constant time <br />
                    O(n) – Linear Time <br />
                    O(n^2) - Quadratic time <br />
                    O(log n) - Logarithmic time <br />
                    O(n log n) – Linearithmic Time <br />
                    O(2^n) – Exponential Time <br />
                    O(n!) – Factorial Time <br />
                    <br />
                    Time Complexity Graph/Chart - 
                    <br /> <br />
                    <img src="https://miro.medium.com/max/1400/1*5ZLci3SuR0zM_QlZOADv8Q.jpeg" height="700px" width="1080px"></img>
                    </M.Typography>
                    </div></M.Grid>
                    <M.Grid item xs={3}>
                        Programming Quotes
                        <br />
                    <M.Divider />
                    <br />
                </M.Grid>
                </M.Grid>
                </M.Container>
    </div>
    )
}