import React from "react";
import * as M from '@mui/material'
import { styled } from '@mui/material/styles';
import { BrowserRouter as Router, Link, Route, Routes } from "react-router-dom";
import axios from 'axios';
import { Editor } from "react-draft-wysiwyg";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";

export const ComplexityCalculator = () => {
    const [value, setValue] = React.useState();
    const [output, setOutput] = React.useState()
    const handleChange = (event) => {
        setValue(event.target.value);
    };

    const handleSubmit = (e) => {
        const id_finder = axios.post('https://time-complexity-calculator.herokuapp.com/api/v1/code/', {"code": value}).then(
            (res) => {
                console.log(res)
                let response = res.data
                let backend_id = response.id
                let code_part = response.code
                setValue("")
                runcode(backend_id, code_part)
            }
        )

        console.log(value)
    }

    const runcode = (id, code_part) => {
        axios.post('https://time-complexity-calculator.herokuapp.com/api/v1/code/' + id + '/runcode/', {"code": code_part}).then(
            (res) => {
                console.log(res)
                setOutput(res.data)
                return res
            }
        )
    }


    return (
        <div className="container">
        <M.Container maxwidth="md">
        <M.Grid container spacing={2}>
        <M.Grid item xs={8}>
        NOTE: 1 microsecond = 1.0E-6 seconds.  (See Time Chart in Get Started Section)
            <div className="editor-box">
                <br></br>
                <M.TextField
                id="outlined-multiline-flexible"
                label="# Enter Your Code Here"
                multiline
                rows={10}
                className="python-editor"
                onChange={handleChange}
                defaultValue="import big_o
def my_func(n):
# Enter your code here
print(big_o.big_o(my_func, big_o.datagen.n_, n_repeats=20, min_n=2, max_n=100)[0])
                "
                />
            </div>
            <br />

        </M.Grid>
        <M.Grid item xs={4}>
            <M.Typography variant="h6" component="body">Output: </M.Typography>
            <M.Divider />
            <div className="output-area">

                <M.Box sx={{
                    width: 720,
                    height: 50,
                    backgroundColor: "#f0f3f3",
                }}>
                    <M.Typography variant="h6" component="body">
                        {output}
                    </M.Typography>
                </M.Box>
            </div>
        </M.Grid>
        <M.Grid item xs={8}>
        <M.Button variant="contained" onClick={handleSubmit}>
                Submit
            </M.Button>
            <p>    </p>
            <M.Button variant="contained">
                Reset
            </M.Button>
            <br /><br />

            
        </M.Grid>
        </M.Grid>
        
        
        </M.Container>
        </div>
    )
}